
<template>
<div>
  <el-table
      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange">
    <el-table-column
        type="selection"
        width="55">
    </el-table-column>
    <el-table-column
        label="日期"
        width="120">
      <template #default="scope">{{ scope.row.date }}</template>
    </el-table-column>
    <el-table-column
        prop="name"
        label="姓名"
        width="120">
    </el-table-column>
    <el-table-column
        prop="address"
        label="地址"
        show-overflow-tooltip>
    </el-table-column>
  </el-table>
  <div style="margin-top: 20px">
    <el-button @click="toggleSelection([tableData[1], tableData[2]])">切换第二、第三行的选中状态</el-button>
    <el-button @click="toggleSelection()">取消选择</el-button>
  </div>
</div>
</template>

<script>
export default {
  name: "CheckboxTable",
  data() {
    return {
      tableData: [{
        date: '2016-05-03',
        name: '小虎',
        address: '湖北省武汉市 森林野生 动物园'
      }, {
        date: '2016-05-02',
        name: '小虎',
        address: '湖北省武汉市 森林野生 动物园'
      }, {
        date: '2016-05-04',
        name: '小虎',
        address: '湖北省武汉市 森林野生 动物园'
      }, {
        date: '2016-05-01',
        name: '小虎',
        address: '湖北省武汉市 森林野生 动物园'
      }, {
        date: '2016-05-08',
        name: '小虎',
        address: '湖北省武汉市 森林野生 动物园'
      }, {
        date: '2016-05-06',
        name: '小虎',
        address: '湖北省武汉市 森林野生 动物园'
      }, {
        date: '2016-05-07',
        name: '小虎',
        address: '湖北省武汉市 森林野生 动物园'
      }],
      multipleSelection: []
    }
  },

  methods: {
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
  }
}
</script>

